import type { LivelinkCore } from "./LivelinkCore";
import type VM from "node:vm";

/**
 *
 */
const isNode = typeof process !== "undefined" && process.versions != null && process.versions.node != null;

/**
 * The URL of the Livelink core library. Injected by the build system.
 */
declare const LIVELINK_CORE_URL: string;

/**
 *
 */
type LivelinkCoreType = typeof LivelinkCore;

/**
 *
 */
type CoreImporter = () => Promise<{
    LivelinkCore: LivelinkCoreType;
    LIVELINK_CORE_VERSION: string;
    LIVELINK_CORE_COMMIT_SHA: string;
}>;

/**
 *
 */
type BuildTarget = "prod" | "dev" | "debug";

/**
 * Dynamically loads the Livelink core library.
 *
 * {@link DynamicLoader.load} must be called before accessing {@link DynamicLoader.Core}.
 *
 * ### Usage
 * ```typescript
 * await DynamicLoader.load();
 * const core = new DynamicLoader.Core();
 * ```
 *
 * @category Core
 */
export class DynamicLoader {
    /**
     *
     */
    static #core: LivelinkCoreType | null = null;

    /**
     *
     */
    static #importPromise: Promise<void> | null = null;

    /**
     * Returns the core.
     */
    static get Core(): LivelinkCoreType {
        if (!DynamicLoader.#core) {
            throw new Error("Livelink core not initialized");
        }
        return DynamicLoader.#core;
    }

    /**
     * Dynamically loads the Livelink core library.
     *
     * Must be called before accessing {@link DynamicLoader.Core}.
     * Only one target can be loaded at a time.
     *
     * @param target - The target build to load.
     *  - `"prod"`: Contains no debug information.
     *  - `"dev"`: Contains some debug information.
     *  - `"debug"`: Contains extra debug information.
     */
    static load(target: BuildTarget = "prod"): Promise<void> {
        if (!DynamicLoader.#importPromise) {
            DynamicLoader.#importPromise = DynamicLoader.#importCore(target);
        }

        return DynamicLoader.#importPromise;
    }

    /**
     *
     */
    static async #importCore(target: BuildTarget): Promise<void> {
        const dynamicImport = await DynamicLoader.#createImporter(target);
        const { LivelinkCore } = await dynamicImport();

        DynamicLoader.#core = LivelinkCore;

        const TARGET_COLOR = {
            prod: "#FFC700",
            dev: "#36D399",
            debug: "#FB4949",
        } satisfies Record<BuildTarget, string>;

        console.info(
            `%c Livelink Core initialized 🚀 %c ${LivelinkCore.VERSION} %c ${LivelinkCore.COMMIT_SHA} %c${target}`,
            "background: #080A12; color: #FFC700; font-weight: bold; padding: 8px; padding-right: 164px; border-radius: 16px; margin: 16px 0 16px 0",
            "background: #9EA6D6; color: #0A1018; font-weight: bold; padding: 1px; padding-right: 0px; margin-left: -160px; border-radius: 16px 0 0 16px;",
            "background: #6973A8; color: #0A1018; font-weight: bold; padding: 1px; padding-left: 0; border-radius: 0 16px 16px 0;",
            `background: ${TARGET_COLOR[target]}; color: #000; font-weight: bold; padding: 6px 16px; border: 4px solid #080A12; border-radius: 16px; margin-left: 16px`,
        );
    }

    /**
     *
     */
    static async #createImporter(target: string): Promise<CoreImporter> {
        const moduleURL = LIVELINK_CORE_URL + "/" + (target !== "prod" ? `core.${target}.mjs` : "core.mjs");

        if (!isNode) {
            return new Function("return import('" + moduleURL + "')") as CoreImporter;
        }

        const response = await fetch(moduleURL);
        const source = await response.text();

        // As a platform neutral build, we needs to load node:vm dynamically.
        // We can't use await import('node:vm') directly because it will be resolved at build time
        // and will not be available in the browser.
        const vm = (await new Function("return import('node:vm')")()) as {
            SourceTextModule: typeof VM.SourceTextModule;
        };

        // VM Modules is an experimental feature, so it may not be available in all versions of Node.js.
        // This feature is only available with the --experimental-vm-modules command flag enabled.
        if (!vm.SourceTextModule) {
            return new Function("return import('data:text/javascript;base64," + btoa(source) + "')") as CoreImporter;
        }

        console.info("Using experimental VM SourceTextModule to load Livelink core");
        return (async () => {
            const module = new vm.SourceTextModule(source, { identifier: moduleURL });
            // @ts-expect-error: Since core has no dependencies, the linker function will never be called.
            await module.link(() => {});
            await module.evaluate();

            return module.namespace;
        }) as CoreImporter;
    }

    /**
     *
     */
    private constructor() {}
}
